export default {
  props: {
    title: {
      type: String,
      default: "",
    },
    text: {
      type: String,
      default: "",
    },
    confirm: {
      type: Function,
      default: null,
    },
    cancel: {
      type: String,
      default: null,
    },
    show: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    onCancel() {
      if (this.cancel) this.cancel();
      this.show = false;
    },
    onConfirm() {
      if (this.confirm) this.confirm();
      this.show = false;
    },
  },
};
